@font-face {
	font-family: "HelveticaNeueLight";
	src: url(../fonts/HelveticaNeueLight.eot);
	src: url(../fonts/HelveticaNeueLight.woff) format("woff");
	src: url(../fonts/HelveticaNeueLight.ttf) format("truetype"); }

@font-face {
	font-family: "Proxima";
	src: url(../fonts/ProximaNovaRegular.eot);
	src: url(../fonts/ProximaNovaRegular.woff) format("woff");
	src: url(../fonts/ProximaNovaRegular.ttf) format("truetype"); }

@import "../../_styles/variables.sass";
@import "bootstrap/_bootstrap.scss";
// Подключение модулей
@import "../_modules/nav/nav";
@import "../_modules/header/header";
@import "../_modules/catalog/catalog";
@import "../_modules/product/product";
@import "../_modules/sidebar/sidebar";
@import "../_modules/footer/footer";

html {
  font-size: 62.5%; }

body {
  margin: 0;
  padding: 0;
  font-family: "Proxima", sans-serif;
  font-size: 1.6rem; }

@media (max-width: 991px) {
	body {
		padding-top: 50px; } }

ul {
	padding: 0;
	margin: 0; }

h1, h2, h3, h4 {
	margin: 0;
	padding: 0;
	font-family: "Helvetica Neue", Helvetica; }

a {
	transition: .5s;
	outline: none !important;
	&:hover {
		color: $color-main;
		text-decoration: none; } }



.social {
	&__left {
    	float: left; }
	&__image {
		margin-right: 20px; } }

button.btn {
	background: none;
	cursor: pointer; }

.btn {
	border: 1px solid $color-main;
	display: block;
	width: 140px;
	padding: 9px 0;
	font-size: 1.4rem;
	text-align: center;
	color: #000;
	transition: .3s;
	clear: both;
	&__search {
		width: 35px;
		height: 30px;
		border: 1px solid $color-1;
		background: url('../images/nav__search.png') top no-repeat;
		background-position: 50% 45%;
		cursor: pointer; }
	&__header {
		color: #fff;
		margin-top: 60px; }
	&__cart {
		width: 163px;
		margin: 0 auto; }

	&:hover {
		background: $color-main;
		color: #fff;
		text-decoration: none; }

	&__gray {
		border: 1px solid #787878;
		color: #848484;
		width: 90px;
		height: 25px;
		display: block;
		background: none;
		font-size: 1.4rem;
		text-align: center;
		cursor: pointer; }

	&__account {
		float: left; }

	&__order {
		margin-top: 25px;
		&_login {
			float: left;
			margin-right: 15px; } }

	&__filter {
		margin-bottom: 30px;
		width: 175px;
		cursor: pointer;
		&:before {
			content: url('../images/sidebar/burger.png');
			margin-right: 15px; }
		&_clear {
			background: $color-main;
			width: 100%;
			color: #fff;
			&:hover {
				background: none;
				color: #000; } } }
	&__active {
		background: $color-main;
		color: #fff;
		&:before {
			content: "X"; } }
	&__forgot {
		color: #848484;
		font-size: 1.4rem; }

	&__modal {
		width: 175px; }
	&__catalog {
		position: absolute;
		bottom: -85px; } }
.btn__article {
	width: 320px;
	margin-top: 25px; }
@media (max-width: 991px) {
	.btn__catalog {
		position: static;
		margin-top: 25px; } }
@media (max-width: 410px) {
	.btn__article {
		width: 100%;
		padding: 5px 15px; } }

.borderline {
	height: 1px;
	width: 30px;
	border-bottom: 1px solid $color-main;
	margin: 15px 0;

	&__yellow {
		margin: 10px 0; } }

@media (max-width: 650px) {
	.btn__header {
		margin-top: 95px; } }

@media (max-width: 390px) {
	.btn__header {
		margin-top: 45px; } }

.productItem {
	@extend .col-md-3;
	@extend .col-xs-6;
	margin-bottom: 60px;
	&:hover {
		text-decoration: none; }

	&__background {
		position: relative;
		width: 75%;
		height: 135px;
		margin: 0 auto;
		&_yellow {
			background: #f7f96d; }
		&_blue {
			background: #6dc0f9; }
		&_green {
			background: #8ee94b; } }

	&__image {
		@extend .img-responsive;
		position: absolute;
		left: -15%;
		bottom: 10%;
		max-width: 125%;
		&_white {
			margin: 0 auto;
			margin-bottom: 25px;
			@extend .img-responsive; }
		&_common {
			position: absolute;
			left: -40%;
			top: 10%; }
		&_medium {
			margin: 0 auto;
			padding-top: 10%;
			@extend .img-responsive; } }
	&__title, &__info, &__price {
		margin-left: 33px; }
	&__title, &__info {
		font: {
			size: 1.4rem;
			weight: 400; }
		color: $color-text; }

	&__title {
		text-transform: uppercase;
		margin-top: 15px;
		&_center {
			color: $color-text;
			display: inline-block;
			margin-bottom: 10px; }
		&_large {
			font: {
				size: 2.8rem;
				weight: bold; }
			margin-bottom: 20px; } }

	&__info {
		margin-bottom: 10px; }

	&__price {
		font: {
			size: 1.4rem;
			weight: bold; }
		color: $color-text;

		&_center {
			font-weight: bold; } }

	&_medium {
		@extend .col-md-6;
		margin-left: -30px;
		overflow: hidden; }

	&__block {
		height: 268px;
		float: left;
		width: 50%;
		&_gray {
			background: #f4f6f7; }
		&_common {
			position: absolute;
			right: 0;
			bottom: 10%;
			width: 45%; } }
	&_large {
		@extend .col-md-6;
		height: 535px;
		margin-right: 15px;
		position: relative; }

	&__catalog {
		font: {
			size: 3rem;
			weight: bold; }
		text-transform: uppercase;
		color: #898989; }

	&_padding {
		padding: 20px; } }

@media (min-width: 991px) {
	.productItem {
		margin-bottom: 0; } }
@media (min-width: 991px) and (max-width: 1200px) {
	.productItem {
		&__title {
			&_large {
				font-size: 2rem; } }
		&__image {
			&_common {
				position: initial;
				max-width: 100%;
				padding: 50px 0px; } }

		&__block {
			&_common {
				position: initial;
				width: 100%; } }

		&__title {
			&_general {
				font-size: 3.5rem; } } } }

@media (max-width: 991px) {
	.productItem {
		&__image {
			max-width: 100%;
			margin: 0 auto;
			left: 0%;
			bottom: -15%;
			&_common {
				width: 100%;
				padding: 25px 0;
				position: initial; } }
		&_large {
			height: initial;
			padding-bottom: 25px;
			margin-right: 0;
			margin-bottom: 25px; }
		&_medium {
			margin: 15px -15px; }

		&__block {
			&_common {
				width: initial;
				position: static; } }

		&__background {
			width: 100%;
			height: 155px;
			padding: 25px; }

		&__title, &__info, &__price {
			margin-left: 0; } } }

@media (max-width: 600px) {
	.productItem {
		&__block {
			width: 100%; }
		&__image {
			position: initial; } } }

.popular {
	text-align: center;
	margin-bottom: 110px;
	overflow: hidden;
	clear: both;
	&__title {
		margin-top: 110px;
		margin-bottom: 55px;
		font-size: 2.8rem; } }

.wholesale {
	background: $blue;
	padding: 125px 0;
	margin-bottom: 130px;

	&__contain {
		position: relative; }

	&__title {
		font: {
			size: 4rem;
			weight: bold; }
		color: #fff;
		margin-bottom: 35px; }

	&__block {
		@extend .col-md-5; }

	&__image {
		position: absolute;
		top: -100%;
		right: 0; } }

@media (max-width: 991px) {
	.wholesale {
		&__image {
			display: none; } } }

@media (max-width: 340px) {
	.wholesale__title {
		font-size: 2.8rem; } }


.video {
	&__block {
		@extend .col-md-6;
		@extend .col-md-offset-1;
		height: 500px; }

	&__text {
		@extend .col-md-offset-1;
		@extend .col-md-4;
		&_title {
			color: #4f4f4f;
			font: {
				size: 3.5rem;
				weight: bold; } }
		& p:nth-of-type(even) {
			color: #838383; } }
	&__background {
		height: 435px; } }

@media (max-width: 550px) {
	.video {
		&__background {
			height: 100%; } } }

@media (max-width: 991px) {
	.video {
		&__block {
			height: initial;
			margin-bottom: 25px; }
		&__image {
			position: initial;
			max-width: 100%; } } }

.content__toolbar,
.sidebar__toolbar {
	padding: 15px 0;
	margin: 25px 0; }

.content {
	overflow: hidden;
	&__toolbar {
		overflow: hidden;
		border-bottom: 1px solid #cacaca; }
	&__sort {
		float: left;
		&_select {
			border: 1px solid #fff;
			width: 135px; } }
	&__switch {
		width: 20px;
		height: 20px;
		float: left;
		margin-right: 15px;
		cursor: pointer;
		&_right {
			float: right; } } }
.toolbar {
	&__title {
		font: {
			weight: bold;
			size: 1.6rem; } } }


.feature {
	&s {
		clear: both;
		overflow: hidden;
		margin: {
			bottom: 60px; }
		padding-top: 60px; }
	@extend .col-md-3;
	@extend .col-sm-6;
	margin-bottom: 25px;
	text-align: center;
	&__image {
		@extend .img-responsive;
		width: 60px;
		height: 60px;
		margin: 0 auto; }
	&__title {
		font: {
			size: 1.6rem;
			weight: bold;
			family: 'Helvetica Neue', Helvetica; }
		margin: 10px 0; }
	&__content {
		font-size: 12px; } }

.section__title {
	font: {
		size: 1.6rem;
		weight: bold;
		family: 'Helvetica Neue', Helvetica; }
	border-bottom: 1px solid $color-main;
	padding: 15px 0;
	margin-bottom: 25px; }


.account {
	@extend .col-md-9;

	&__hello {
		color: #6e6e6e;
		font: {
			size: 2.4rem;
			weight: bold;
			family: 'Helvetica Neue', Helvetica; }
		margin: 25px 0; }

	&__title {
		color: #6e6e6e;
		font: {
			size: 2.4rem;
			family: "HelveticaNeueLight"; }
		border-bottom: 1px solid $color-main;
		padding-bottom: 15px;
		margin-bottom: 25px; }

	&__input {
		border: 1px solid $color-main;
		color: #7e7e7e;
		width: 225px;
		height: 35px;
		padding-left: 20px;
		margin-right: 15px;
		&:active {
			color: #000; }
		&:last-of-type() {
			margin-right: 0; } }

	&__select {
		border: 1px solid $color-main;
		color: #7e7e7e;
		padding: 0 10px;
		height: 35px; }

	&__block {
		position: relative;
		padding-left: 140px;
		margin-bottom: 25px;
		&:before {
			content: attr(data-content) "";
			position: absolute;
			left: 0; } }
	&__radio {
		display: none;
		&:checked + .btn__gray {
			color: #fff;
			background: $color-main;
			border: 1px solid $color-main; } }

	&__common {
		border-bottom: 1px solid $color-main;
		margin-bottom: 25px; }

	&__name, &__other {
		margin-bottom: 25px;
		overflow: hidden; } }

@media (max-width: 1200px) {
	.account {
		&__input {
			width: 200px; } } }

@media (max-width: 727px) {
	.account {
		&__input {
			display: block;
			margin-bottom: 25px; } } }
@media (max-width: 525px) {
	.account {
		&__block {
			padding-left: 100px; } } }

@media (max-width: 440px) {
	.account {
		&__block {
			padding-left: 0;
			&:before {
				display: block;
				position: initial;
				margin-bottom: 15px; } } } }

table {
	width: 100%;
	text-align: center;
	vertical-align: middle;
	border-collapse: collapse; }

.table {
	&__text_center {
		text-align: center; } }
.table__account {
	margin-bottom: 35px;
	&_row {
		border-bottom: 1px solid $color-main; }
	&_cell {
		padding: 25px 0; }
	&_order {
		clear: both;
		overflow: hidden; }
	&_image {
		@extend .img-responsive;
		clear: both;
		float: left; }
	&_price {
		font-weight: bold; }
	&_title {
		text-transform: uppercase; } }

@media (max-width: 991px) {
	.table__account {
		& thead {
			display: none; }

		& td {
			display: block;
			&:first-of-type() {
				&:before {
					padding: 0; } }
			&:before {
				content: attr(data-head) "";
				font-weight: bold;
				color: #000;
				margin-right: 15px; } }

		& tr {
			border-bottom: 1px solid $color-main; }

		&_cell, &_text {
			text-align: center;
			width: initial; }

		&_image {
			display: block;
			margin: 15px auto;
			float: initial; }
		&_bottom {
			padding-bottom: 0; }
		&_top {
			padding-top: 0; } } }

.table__cart {
	padding-bottom: 15px;
	margin-bottom: 10px;
	text-align: left;
	& td {
		vertical-align: top;
		&:nth-of-type(1) {
			text-align: left;
			width: 60%; }
		&:nth-of-type(3) {}

		&:nth-of-type(3), &:nth-of-type(4) {
			text-align: center; } }
	& thead {
		border-bottom: 25px solid transparent;
		font-size: 1.8rem;
		text-align: left; }
	&_image {
		@extend .img-responsive;
		width: 150px;
		height: 150px;
		float: left;
		margin-right: 25px; }

	&_price, &_total {
		font-weight: bold;
		font-size: 1.6rem; }

	&_price {
		color: #757575; }

	&_input {
		width: 40px;
		border: none;
		font: {
			size: 1.8rem;
			weight: bold; }
		text-align: center;
		outline: none;
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		float: left;
		&::-webkit-outer-spin-button,&::-webkit-inner-spin-button,&::-o-outer-spin-button,&::-o-inner-spin-button {
			-webkit-appearance: none;
			-o-appearance: none;
			margin: 0; } }
	&_row {
		border-bottom: 15px solid transparent; }

	&_title {
		text-transform: uppercase;
		font-size: 1.4rem; }
	&_subtitle {
		text-transform: lowercase; }

	&_text {
		color: #848484;
		font-size: 1.2rem;
		width: 430px; }
	&_total {
		width: 120px; }
	&_remove {
		font-size: 1.8rem;
		color: #757575; }
	&_count {
		width: 70px;
		overflow: hidden; } }

@media (max-width: 991px) {
	.table__cart {
		& thead {
			display: none; }

		& td {
			display: block;
			padding: 15px;
			width: 100% !important;
			&:first-of-type() {
				text-align: center;
				&:before {
					display: block;
					margin: 0;
					margin-bottom: 10px; } }
			&:nth-of-type(3) {
				background: lighten(#000, 95%); }
			&:before {
				content: attr(data-head) "";
				font-weight: bold;
				color: #000;
				margin-right: 15px;
				float: left; } }

		& tr {
			border-bottom: 1px solid $color-main; }

		&_text {
			text-align: center;
			width: initial; }

		&_image {
			display: block;
			margin: 15px auto;
			float: initial; }

		&_total, &_price {
			text-align: left !important; }

		&_remove {
			text-align: center;
			font-weight: bold;
			font-size: 2.4rem;
			display: block; } } }

@media (max-width: 625px) {
	.cart__sum {
		float: initial !important;
		margin: 25px 0; } }

.cart {
	&__total {
		border-top: 1px solid $color-main;
		padding-top: 15px;
		margin-top: 15px; }

	&__title {
		margin: 30px 0; }

	&_radio {
		margin-right: 10px; }
	&__sum {
		font: {
			size: 1.6rem;
			weight: bold; }
		float: right;

		&_black {
			font: {
				size: 2.4rem;
				weight: bold; } }

		&_black, &_left {
			display: inline; }

		&_left {
			margin-right: 5px; } } }

@media (max-width: 991px) {
	.cart {
		&__total {
			border-top: 0; } } }

.order {
	&__type {
		@extend .col-md-4;
		&_radio {
			margin: {
				right: 10px;
				left: -23px; } } }
	&__label {
		margin-bottom: 10px;
		display: block;
		&_remember {
			font: {
				size: 1.4rem;
				weight: 400; }
			display: block;
			margin: 20px 0; } }
	&__radio {
		margin: {
			right: 15px;
			bottom: 25px; }
		&_small {
			font-size: 1.4rem;
			margin-bottom: 5px; } }
	&__input, &__textarea {
		border: 1px solid $color-main;
		color: #7e7e7e;
		width: 265px;
		height: 35px;
		padding-left: 10px;
		margin-bottom: 25px;
		&:active {
			color: #000; } }

	&__textarea {
		max-width: 265px;
		height: 160px;
		max-height: 160px; }

	&__contacts p {
		&:first-of-type() {
			margin: 0;
			margin-bottom: 5px; }

		margin: 5px 0;

		&:last-of-type() {
			margin-bottom: 15px; } }

	&__title, &__label {
		font: {
			size: 1.6rem;
			weight: bold;
			family: "Helvetica Neue", Helvetica; } }

	&__title, &__text {
		margin-bottom: 15px; }

	&__text {
		font-size: 1.4rem;
		color: #696969; }

	&__total {
		border-top: 1px solid $color-main;
		padding-top: 15px;
		margin-bottom: 15px;
		clear: both;
		font: {
			size: 2.4rem;
			weight: bold; }
		color: #818181;
		&_black {
			color: #000;
			margin: 0 15px; } } }


@media (max-width: 991px) {
	.order {
		&__textarea {
			display: block; } } }

.article {
	@extend .col-md-8;
	&__gray {
		background: #ebebeb;
		padding: 25px 75px;
		margin: 25px -75px;
		overflow: hidden;
		&_list {
			list-style: none; }
		&_item {
			margin: 6px 0;
			font-size: 1.4rem; }
		&_title {
			@extend .col-md-3;
			font: {
				size: 4.6rem;
				weight: bold; } }
		&_image {
			@extend .col-md-4, .img-responsive; }
		&_description {
			@extend .col-md-5; } }
	&__title {
		font-size: 3rem;
		margin: 25px 0;
		display: block;
		&_center {
			text-align: center;
			margin-bottom: 70px; } }

	&__spoiler {
		border-left: 5px solid #0c00ff;
		padding-left: 25px; }

	&__image {
		@extend .img-responsive;
		margin: 15px 25px;
		&_left {
			float: left;
			margin-left: 0; }
		&_right {
			float: right;
			margin-right: 0; }
		&_smaller {
			float: left; } }

	&__source {
		color: #828282;
		&_link {
			color: #000;
			margin-left: 10px; } }

	&__page {
		border-bottom: 1px solid #d4d4d4;
		overflow: hidden;
		padding: 15px 0; }

	&__link {
		&_left {
			float: left; }
		&_right {
			float: right; } }

	&__timeline {
		@extend .col-md-6;
		border-bottom: 1px solid $color-main;
		padding: 15px 0;
		&:nth-of-type(odd) {
			clear: both;
			margin-right: -1px;
			margin-top: -30px;
			border-right: 1px solid $color-main; }
		&:nth-of-type(even) {
			border-left: 1px solid $color-main; } }
	&__year {
		padding: 0 15px;
		font: {
			size: 3rem;
			weight: bold; }
		color: $color-main;
		&:nth-of-type(odd) {
			float: left; }

