.slider {
	&-handle {
		background-color: #7a142a;
		background-image: initial;
		width: 10px;
		height: 10px;
		cursor: pointer; }
	&-track {
		&-low, &-high {
			background: #cacaca; } }
	&-track, &-horizontal {
		height: 3px !important;
