	&-selection {
		background: lighten($color-main, 2%); } }

.arrow {
	width: 7px;
	padding: 0;
	height: 12px;
	border: none;
	outline: none;
	cursor: pointer;
	display: block;
	float: left;
	margin-top: 7px;
	&__left {
		background: url(../images/catalog/arrow__left.png) no-repeat; }
	&__right {
		background: url(../images/catalog/arrow__right.png) no-repeat; }
	&__up {
