		&:nth-of-type(1) {
			border-bottom: 0; } } }
.slick {
	&-slide {
		outline: none; }
	&-prev, &-next {
		border: 1px solid $color-main;
		width: 30px;
		height: 30px; }
	&-prev:before, &-next:before {
		font-size: initial;
		width: 6px;
		height: 19px;
		display: block;
		margin: 0 auto; }
	&-prev {
		&:before {
			content: url(../images/catalog/arrow__left.png); } }
	&-next {
