@import "../../_styles/variables.sass";

.sidebar {
	@extend .col-md-3;
	&__block {
		clear: both;
		overflow: hidden;
		margin-bottom: 30px; }
	&__age {
		border: 1px solid $color-main;
		float: left;
		width: 50px;
		padding: 3px 0;
		text-align: center;
		margin: {
			right: 15px;
			bottom: 15px; }
		transition: .3s;
		cursor: pointer;
		&:hover {
			background: $color-main;
			color: #fff; } }

	&__hidden {
		display: none;
		&:checked + .sidebar__age {
			background: $color-main;
			color: #fff; }
		&:checked + .sidebar__item_label {
			color: $color-main;
			&:before {
				content: url('../images/sidebar/arrow.png');
				position: absolute;
				left: 0; } } }
	&__title {
		margin: 10px 0; }

	&__additional {
		border: 1px solid #cacaca;
		padding: 20px;
		clear: both;
		display: none; }

	&__list {
		list-style: none;
		margin: 20px 0;
		&:first-of-type() {
			margin: 0;
			margin-bottom: 20px; } }
	&__item {
		font-size: 1.4rem;
		color: #848484;
		margin: 5px 0;
		padding-left: 15px;
		position: relative;
		&_label {
			cursor: pointer; }
		&:hover {
			color: #000; } }
	&__account {
		&_list {
			list-style: none;
			margin-left: -15px; }
		&_item {
			margin: 15px 0; } }
	&__link {
		color: #a1a1a1;

		&_medium, &_large {
			font-weight: bold;
			margin: 15px 0;
			display: block; }

		&_medium {
			font: {
				size: 1.6rem; } }
		&_large {
			font: {
				size: 3rem; } } }

	&__title {
		font: {
			size: 1.8rem;
			weight: bold; } }
	&__date {
		font-size: 1.2rem; } }

.sidebar__article {
	@extend .col-md-4;
	&_image {
		@extend .img-responsive; } }

@media (max-width: 991px) {
	.sidebar {
		&__account {
			min-height: 1px;
			margin-bottom: 25px; } } }
