@import "../../_styles/variables.sass";
.nav {
	@extend .hidden-xs;
	@extend .hidden-sm;
	&__info {
		@extend .container-fluid;
		background: $gray;
		padding: 10px 0;
		font: {
			size: 1.4rem; } }

	&__left {
		float: left; }

	&__right {
		float: right; }

	&__auth {
		@extend .nav__left;
		border-left: 1px solid $color-main;
		padding-left: 20px;
		margin-left: 20px;
		&_link {
			font: {
				size: 1.4rem;
				weight: bold; }
			color: #000; } }
	&__list {
		font: {
			weight: bold; }
		padding: 15px 0;
		border-top: 1px solid $color-nav; }

	&__item {
		display: inline-block;
		margin-right: 35px;

		&_link {
			color: #000; } }

	&__logo {
		@extend .img-responsive, .col-sm-2;
		margin: {
			right: -15px;
			left: -15px; } }

	&__search {
		@extend .col-sm-offset-3, .col-sm-3;
		margin-top: 11px;
		&_input {
			width: 220px;
			height: 30px;
			border: 1px solid $color-1;
			border-right: 0; } }

	&__contain {
		@extend .container; }

	&__content {
		margin: 15px 0; }

	&__cart {
		overflow: hidden;
		padding-left: 175px;
		margin-top: 4px;
		&:before {
			content: url('../images/nav__cart.png');
			float: left;
			margin-right: 20px; }

		&_link {
			color: #3b3b3b; }

		&_info {
			font-weight: bold; } }

	&__mobile {
		@extend .hidden-md;
		@extend .hidden-lg;
		background: $color-main;
		position: fixed;
		right: 0;
		left: 0;
		top: 0;
		z-index: 9999;
		height: 50px;
		border-bottom: 1px solid darken($color-main, 10%);
		&_list {
			position: fixed;
			top: 50px;
			left: 0;
			right: 0;
			background: $color-main;
			display: none; }

		&_item {
			border-bottom: 1px solid darken($color-main, 10%);
			padding: 25px 40px; }

		&_link {
			color: darken($color-main, 30%);
			font-size: 2rem;
			&:hover {
				color: #fff; } }

		&_button {
			float: right;
			width: 35px;
			margin-right: 15px;
			margin-top: 10px;
			cursor: pointer;
			height: 30px; }

		&_title {
			color: #fff;
			font-size: 2rem;
			text-transform: uppercase;
			font-weight: bold;
			padding: 10px 20px;
			float: left;
			&:hover {
				color: darken($color-main, 25%); } } } }

@media (min-width: 991px) and (max-width: 1200px) {
	.nav {
		&__search {
			&_input {
				width: 170px; } }
		&__cart {
			padding-left: 105px; } } }
