@import "../../_styles/variables.sass";

.header {
	background: url('../images/header__background.jpg') top no-repeat;
	height: 550px;

	&__contain {
		@extend .container;
		color: #fff; }

	&__title {
		font: {
			size: 4.8rem;
			weight: bold; } }

	&__text {
		font: {
			size: 2.2rem;
			family: HelveticaNeueLight; }
		line-height: 32px;
		margin-bottom: 35px; }

	&__block {
		@extend .col-md-4;
		@extend .col-xs-7;
		margin-top: 150px; }

	&__breadcrumbs {
		list-style: none;

		& li {
			display: inline-block;
			&:after {
				content: '/';
				margin: 0 10px; }

			&:last-of-type():after {
				content: ''; } }

		&_link {
			color: #b1b1b1; }

		&_active {
			color: #fff; } } }

.header__slider {
	position: relative;
	height: 540px;
	margin-bottom: 40px;
	&_image {
		position: absolute;
		@extend .img-responsive; }
	&_title {
		font: {
			size: 3rem;
			weight: bold; }
		color: #fff;
		position: absolute;
		left: 25px;
		bottom: 15%;
		width: 30%; } }

.header__timeline {
	background: #eef6f8;
	height: 335px;
	margin-bottom: 60px; }

.header__article {
	&_image {
		display: block;
		margin: 0 auto;
		@extend .img-responsive; } }
@media (max-width: 991px) {
	.header__slider {
		overflow: hidden;
		&_image {
			max-width: initial; } }
	.header__timeline {
		height: 100%; } }
@media (max-width: 650px) {
	.header__block {
		margin-top: 100px; } }
@media (max-width: 530px) {
	.header {
		&__block {
			margin-top: 90px; } } }
