@import "../../_styles/variables.sass";

.footer {
	background: $gray;
	padding: 35px 0;
	margin-top: 70px;
	&__title {
		font: {
			size: 1.6rem;
			weight: bold; } }
	&__block {
		@extend .col-md-4;
		@extend .col-sm-6;
		@extend .col-xs-12; }

	&__list {
		list-style: none;
		float: left;
		&_notitle {
			margin-left: 50px; } }
	&__item {
		display: block;
		margin: 2px 0; }

	&__link {
		color: #595959;
		font-size: 1.4rem; }

	&__socials {
		padding-left: 50px;
		margin-top: -55px;
		float: left; } }


@media (max-width: 991px) {
	.footer {
		&__list {
			&_notitle {
				margin-left: 50px; } }
		&__block {
			margin-bottom: 50px; }

		&__socials {
			padding-left: 200px; } } }

@media (max-width: 1200px) {
	.footer {
		&__socials {
			padding-left: 150px;
			margin-top: -105px;
			float: left; }
		&__link {
			font-size: 1.5rem; } } }

@media (max-width: 727px) {
	.footer {
		&__socials {
			clear: both;
			margin-top: 20px;
			padding-left: 0; } } }

@media (max-width: 465px) {
	.footer {
		&__list {
			clear: both;
			margin-bottom: 25px;
			&_notitle {
				margin: 0;
				margin-bottom: 25px; } } } }
