@import "../../_styles/variables.sass";

.product {
	margin: {
		top: 30px;
		bottom: 30px; }
	&__thumbmail {
		@extend .col-md-6;
		&_large {
			@extend .img-responsive; }
		&_pager {
			width: 75%;
			padding: 0 25px;
			margin: 0 auto;
			margin-top: 40px;
			transition: .4s; } }
	&__info {
		@extend .col-md-6;
		margin-bottom: 60px; }
	&__title {
		font: {
			size: 2.4rem;
			weight: 400; }
		color: #5e5e5e; }

	&__price {
		font: {
			size: 2.4rem;
			weight: bold; }
		color: #3e3e3e;
		float: left;
		margin-bottom: 25px; }

	&__buy {
		margin: 30px 0; }

	&__count {
		margin: {
			bottom: 25px;
			left: 100px; }
		float: left;
		width: 50px;
		height: 35px;
		border: 1px solid $color-main;
		font: {
			size: 2rem;
			weight: bold; }
		color: #3e3e3e;
		text-align: center; }
	&__tab {
		&_control {
			border-bottom: 1px solid $color-main;
			padding-bottom: 15px;
			margin-top: 20px;
			list-style: none; }
		&_link {
			font: {
				size: 1.6rem;
				weight: bold; }
			color: #8c8c8c;
			margin: {
				right: 20px; } }
		&_active > a {
			color: #3e3e3e;
			outline: 0;
			text-decoration: none;
			&:visited {
				outline: none; } }
		&_text {
			margin-top: 30px;
			color: #3e3e3e;
			font-size: 1.4rem;

			& p:nth-of-type(even) {
				color: #8c8c8c; } }

		&_item {
			display: inline-block; } }
	&__button {
		position: relative;
		left: -13px;
		width: 6px;
		float: left; } }

.product__item {
	text-align: center;
	margin-bottom: 50px;
	&_image {
		@extend .img-responsive;
		margin: 0 auto;
		margin-bottom: 15px; }

	&_title, &_count {
		color: #5e5e5e;
		font: {
			size: 1.4rem;
			weight: 400;
			family: "Helvetica Neue", Helvetica; } }

	&_title {
		text-transform: uppercase;
		margin-bottom: 5px;
		transition: .3s;
		&:hover {
			color: $color-main; } }
	&_price {
		font: {
			weight: bold;
			size: 1.6rem; }
		margin: 15px 0; } }

.product__layout_grid {
	@extend .col-md-4;
	@extend .col-sm-6; }

.product__layout_list {
	@extend .col-xs-12; }

.product__layout_list {
	&.product__item {
		text-align: left;
		& > a > .product__item_title {
			font: {
				size: 1.6rem;
				weight: bold; }
			float: left;
			margin-right: 5px; }
		& > a > .product__item_image {
			margin: 0;
			margin-right: 25px;
			float: left; }

		& > .product__item_price {
			font-size: 1.8rem; }


		& > .product__item_text, .product__item_spec {
			text-align: left; }

		& > .product__item_text {
			font-size: 1.4rem;
			color: #818181; }

		& > .product__item_spec {
			list-style: none;
			max-width: 500px;
			margin: 25px 0;
			margin-left: 255px;
			.product__item_specItem {
				position: relative;
				padding-left: 50%;
				margin: 5px 0;
				&:before {
					position: absolute;
					left: 30%;
					content: attr(data-content) ""; } } }
		& > .btn {
			float: right; } } }
.product__related {
	@extend .col-md-3;
	@extend .col-sm-6;
	@extend .col-xs-12;
	&_items {
		position: relative; }
	&_toolbar {
		position: relative;
		top: -55px;
		& > .slick-prev {
			left: initial;
			right: 29px; }
		& > .slick-next {
			right: 0; }
		& .slic-arrow {
			position: relative; } } }

.product__table {
	& td {
		color: #000;
		text-align: left;
		padding: 5px 0;
		&:nth-of-type(odd) {
			color: #5e5e5e; } } }
@media (min-width: 991px) {
	.product__item {
		margin-bottom: 0; } }
@media (max-width: 767px) {

	.product__layout_list {
		border-bottom: 1px solid $color-main;
		padding-bottom: 50px; }

	.product__list {
		&.product__item {
			&_image {
				float: initial;
				margin: 0 auto;
				margin-bottom: 35px; }
			&_count {
				clear: both; } }
		&_spec {
			max-width: initial;
			margin-left: 0; } } }


@media (max-width: 370px) {
	.product {
		&__count {
			margin-left: 0;
			float: right; } } }
