@import "../../_styles/variables.sass";

.catalog {
	background: #f7f7f7;
	&__contain {
		@extend .container;
		padding: 115px 0;
		position: relative; }
	&__block {
		@extend .col-md-3;
		@extend .col-xs-12; }

	&__title {
		margin: 0;
		line-height: 4rem;
		font-size: 2.8rem; }

	&_large {
		margin: {
			top: 60px; } } }

@media (max-width: 991px) {
	.catalog {
		&__block {
			margin-bottom: 50px; }
		&__contain {
			padding: 60px 0 0 0; } } }

@media (min-width: 550px) and (max-width: 991px) {
	.catalog {
		&__block {
			margin-bottom: 60px; } } }
